* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  min-height: 100%;
  background-image: linear-gradient(to right, #6a11cb 0%, #2575fc 100%);
}

Link {
  text-decoration: none;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 8vh;
  background-color: rgb(0, 238, 255);
}

.nav-links {
  display: flex;
  margin-top: 1%;
  justify-content: space-around;
  width: 40%;
}

.nav-links a:hover {
  color: white;
  border-style: solid;
  border-width: 0px 0px 0px 0px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  letter-spacing: 3px;
}

.nav-links li {
  list-style: none;
}

.burger {
  display: none;
  cursor: pointer;
}

.burger div {
  width: 25px;
  height: 3px;
  background-color: darkblue;
  margin: 5px;
}

@media screen and (max-width: 1024px) {
  .nav-links {
    width: 60%;
  }
}

@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden;
  }

  .nav-links {
    position: absolute;
    right: 0px;
    height: 92vh;
    top: 8vh;
    background-color: rgb(0, 238, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: 0.5s ease-in;
  }

  .nav-links li {
    opacity: 0;
  }

  .burger {
    display: block;
  }
}

.nav-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.slider {
  overflow: hidden;
}

.slider figure {
  position: relative;
  max-height: 20%;
  width: 500%;
  margin: 0;
  left: 0;
  -webkit-animation: 30s slider infinite;
          animation: 30s slider infinite;
  text-align: center;
  color: white;
}

.slider figure img {
  width: 20%;
  float: left;
}

@-webkit-keyframes slider {
  0% {
    left: 0;
  }
  20% {
    left: 0;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
}

@keyframes slider {
  0% {
    left: 0;
  }
  20% {
    left: 0;
  }
  25% {
    left: -100%;
  }
  45% {
    left: -100%;
  }
  50% {
    left: -200%;
  }
  70% {
    left: -200%;
  }
  75% {
    left: -300%;
  }
  95% {
    left: -300%;
  }
  100% {
    left: -400%;
  }
}
